(function() {
  var app;

  app = angular.module('app', ['ngRoute', 'ngAnimate', 'appControllers']);

  app.config(function($routeProvider) {
    return $routeProvider.when('/recipes', {
      templateUrl: '../views/recipes-list.html',
      controller: 'RecipesListCtrl'
    }).when('/recipes/:alias', {
      templateUrl: '../views/recipe-detail.html',
      controller: 'RecipeDetailCtrl'
    }).otherwise({
      redirectTo: '/recipes'
    });
  });

}).call(this);

(function() {
  var appControllers;

  appControllers = angular.module('appControllers', []);

  appControllers.controller('RecipesListCtrl', function($scope, $routeParams, $rootScope) {
    $scope.recipes = DATA;
    $scope.onClickItem = function(item) {
      var index;
      index = $scope.recipes.indexOf(item);
      return $rootScope.currenRecipe = item;
    };
    return $scope.isActiveItem = function(alias) {
      return $routeParams.alias === alias;
    };
  });

  appControllers.controller('RecipeDetailCtrl', function($scope, $routeParams, $rootScope) {
    var i;
    if ($routeParams) {
      i = DATA.filter(function(recipe) {
        return recipe.alias === $routeParams.alias;
      });
      return $scope.currenRecipe = i[0];
    } else {
      return $scope.currenRecipe = $rootScope.currenRecipe;
    }
  });

}).call(this);
